import { sanityFetch } from "@/sanity/lib/live"; // or your fetch method
import { urlFor } from "@/sanity/lib/image";
import { PortableText } from "@portabletext/react";
import Image from "next/image";

interface JobDetailPageProps {
    params: {
        slug: string;
    };
}

const JOB_QUERY = `
*[_type == "job" && slug.current == $slug][0] {
  heroimage,
  title,
  body,
  location,
  jobdescription
}
`;

export default async function JobDetailPage({ params }: JobDetailPageProps) {
    const { slug } = params;

    const job = await sanityFetch({ query: JOB_QUERY, params: { slug } });

    if (!job) {
        return <div>Job not found</div>;
    }

    return (
        <main className="w-full">
            {/* Hero Section */}
            {job.heroimage && (
                <div className="relative h-[400px] w-full">
                    <Image
                        src={urlFor(job.heroimage).url()}
                        alt={job.title}
                        layout="fill"
                        objectFit="cover"
                        className="object-center"
                    />
                    <div className="absolute inset-0 bg-black/50 flex flex-col justify-center items-center text-white">
                        <h1 className="text-4xl font-bold">{job.title}</h1>
                        <p className="mt-2 text-lg">{job.location}</p>
                    </div>
                </div>
            )}

            {/* Job Details */}
            <section className="container mx-auto px-4 py-16 max-w-4xl">
                {/* About the Job */}
                <div className="mb-12">
                    <h2 className="text-2xl font-bold mb-4">About the Job</h2>
                    <div className="prose">
                        {job.body && <PortableText value={job.body} />}
                    </div>
                </div>

                {/* Job Description */}
                <div>
                    <h2 className="text-2xl font-bold mb-4">Job Description</h2>
                    <div className="prose">
                        {job.jobdescription && <PortableText value={job.jobdescription} />}
                    </div>
                </div>

                {/* Apply Now Button */}
                <div className="mt-12 text-center">
                    <a
                        href="/apply" // Update to your application URL
                        className="inline-block bg-amber-500 hover:bg-amber-600 text-white font-bold py-3 px-6 rounded-full"
                    >
                        Apply Now
                    </a>
                </div>
            </section>
        </main>
    );
}
